import * as constants from './constants';
import {LoadingManager, FileLoader} from 'three';
import {MTLLoader, OBJLoader} from 'three-obj-mtl-loader'

export const updateGameState = (currentPlayer, players) => dispatch =>{
  dispatch({type: constants.GAME_STATE, payload: { currentPlayer, players}})
}
export const changeOnHover = state => dispatch =>{
  dispatch({type: constants.HOVER_OVER_UI, payload: state});
}
export const changeGameState = (state) => dispatch =>{
  switch (state) {
    case "GAME":
        dispatch({type: constants.GAME_STATE_GAME});
      break;
    case "MAINMENU":
        dispatch({type: constants.GAME_STATE_MAINMENU});
      break;
    case "LOADER":
        dispatch({type: constants.GAME_STATE_LOADER});
      break;
    case "ABOUT":
        dispatch({type: constants.GAME_STATE_ABOUT});
      break;
    default:
        dispatch({type: constants.GAME_STATE_LOADER});
      break;
  }
}
export const worldLoading = (state,error) => dispatch =>{
  switch (state) {
    case true:
      dispatch({type: constants.WORLD_LOADING_PENDING});
      break;
    case false: 
      dispatch({type: constants.WORLD_LOADING_FINISH});
      break;
    default:
        dispatch({type: constants.WORLD_LOADING_ERROR, payload: error});
      break;
  }
}
export const loadingGameContent = () => dispatch =>{
    let models = {},config = [], modelPath = "./models/"
    const manager = new LoadingManager();
    manager.onStart =  () =>{ dispatch({type: constants.GAME_LOADING_PENDING}); };
    manager.onLoad =  ( ) => {  dispatch({type: constants.GAME_LOADING_FINISH, payload: { models, config }    });};
    manager.onError = err => { dispatch({type: constants.GAME_LOADING_ERROR, payload: err});};
    manager.onProgress =  ( url, itemsLoaded, itemsTotal ) => { console.log( `Loading ${itemsLoaded} of ${itemsTotal}` ); };
    
    const configLoader = new FileLoader(manager).setPath('./config/').setResponseType('json');
   
    configLoader.load('config.json', json=>{
         config.push(json);
    });
    const mtlLoader = new MTLLoader(manager).setPath(modelPath);
    
      mtlLoader.load('field.mtl',materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials);
        objLoader.load('field.obj',object=>{
         models.field = {
           geometry: object.children[0].geometry,
           material: object.children[0].material
         }
        });
      });

      mtlLoader.load('cloud.mtl',materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
        objLoader.load('cloud.obj',object=>{
         models.cloud = {
           geometry: object.children[0].geometry,
           material: object.children[0].material
         }
        });
      });

      mtlLoader.load('wall_default.mtl',materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
        objLoader.load('wall_default.obj',object=>{
         models.wall = {
           geometry: [object.children[0].geometry],
           material: [object.children[0].material]
         }
        });
      });

      mtlLoader.load('tree_one.mtl',materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
        objLoader.load('tree_one.obj',object=>{
         models.tree = {
           geometry: [object.children[0].geometry],
           material: [object.children[0].material]
         }
         mtlLoader.load('tree_two.mtl',materials=>{
          materials.preload();
          const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
          objLoader.load('tree_two.obj',object=>{
           models.tree.geometry.push(object.children[0].geometry);
           models.tree.material.push(object.children[0].material);
          });
        });
        });
      });

      mtlLoader.load('water.mtl',materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
        objLoader.load('water.obj',object=>{
         models.water = {
           geometry: [object.children[0].geometry],
           material: [object.children[0].material]
         }
         mtlLoader.load('water_two.mtl',materials=>{
          materials.preload();
          const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
          objLoader.load('water_two.obj',object=>{
           models.water.geometry.push(object.children[0].geometry);
           models.water.material.push(object.children[0].material);
          });
        });
        mtlLoader.load('water_one.mtl',materials=>{
          materials.preload();
          const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
          objLoader.load('water_one.obj',object=>{
           models.water.geometry.push(object.children[0].geometry);
           models.water.material.push(object.children[0].material);
          });
        });
        mtlLoader.load('water_none.mtl',materials=>{
          materials.preload();
          const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
          objLoader.load('water_none.obj',object=>{
           models.water.geometry.push(object.children[0].geometry);
           models.water.material.push(object.children[0].material);
          });
        });

        });
      });




      mtlLoader.load('slector.mtl',materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
        objLoader.load('slector.obj',object=>{
         models.slector = {
           geometry: object.children[0].geometry,
           material: [object.children[0].material]
         }
         mtlLoader.load('slectorFaction.mtl',materials=>{
          materials.preload();
          models.slector.material.push(materials.materials.Slector)
        });
        });
      });

      mtlLoader.load('ocean.mtl',materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
        objLoader.load('ocean.obj',object=>{
         models.ocean = {
           geometry: [object.children[0].geometry],
           material: [object.children[0].material]
         }
         mtlLoader.load('ocean_two.mtl',materials=>{
          materials.preload();
          const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
          objLoader.load('ocean_two.obj',object=>{
           models.ocean.geometry.push(object.children[0].geometry);
           models.ocean.material.push(object.children[0].material);
          });
        });
        mtlLoader.load('ocean_one.mtl',materials=>{
          materials.preload();
          const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
          objLoader.load('ocean_one.obj',object=>{
           models.ocean.geometry.push(object.children[0].geometry);
           models.ocean.material.push(object.children[0].material);
          });
        });
        mtlLoader.load('ocean_none.mtl',materials=>{
          materials.preload();
          const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
          objLoader.load('ocean_none.obj',object=>{
           models.ocean.geometry.push(object.children[0].geometry);
           models.ocean.material.push(object.children[0].material);
          });
        });

        });
      });

      mtlLoader.load('fruit_one.mtl',materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
        objLoader.load('fruit_one.obj',object=>{
         models.fruit = {
           geometry: [object.children[0].geometry],
           material: [object.children[0].material]
         }
        });
      });
    
      
      
}


